import React from 'react';
import styled from 'styled-components';

const New = () => {
    return (
        <Section>
            <h1>New</h1>
        </Section>
    );
};

export default New;



const Section = styled.div`
  max-width: 1920px;
  padding: 0 50px;
  h1{
    color: black;
  }
`
